document.addEventListener("DOMContentLoaded", function (event) {


//product discount async
asyncProductDiscount();
//Async Product Origin

})



//Product discount async

function asyncProductDiscount() {
	var nestedDeal2v2 = new Swiper('#swiper-product-deal-best-sell-nested-2v2', {
		direction: 'horizontal',
		slidesPerView: 4,
		slidesPerColumn: 1.5,
		spaceBetween: 1,
		pagination: {
			el: '.swiper-pagination-product-discount-2',
			type: 'bullets',
		},
		breakpointsInverse: true,
		breakpoints: {
			// when window width is >= 320px
			0: {
				slidesPerView: 2,
				spaceBetween: 5,
				slidesPerColumn: 1,
			},
			// when window width is >= 480px
			576: {
				slidesPerView: 2,
				spaceBetween: 5,
				slidesPerColumn: 1.5,
			},
			992: {
				slidesPerView: 3,
				slidesPerColumn: 2,
			},
			// when window width is >= 640px
			1200: {
				slidesPerView: 4,
				slidesPerColumn: 2,
			}
		}
	})
	var nestedDeal2v2 = new Swiper('#swiper-product-deal-coming-nested-2v2', {
		direction: 'horizontal',
		slidesPerView: 4,
		slidesPerColumn: 1.5,
		spaceBetween: 1,
		pagination: {
			el: '.swiper-pagination-product-discount-3',
			type: 'bullets',
		},
		breakpointsInverse: true,
		breakpoints: {
			// when window width is >= 320px
			0: {
				slidesPerView: 2,
				spaceBetween: 5,
				slidesPerColumn: 1,
			},
			// when window width is >= 480px
			576: {
				slidesPerView: 2,
				spaceBetween: 5,
				slidesPerColumn: 1.5,
			},
			992: {
				slidesPerView: 3,
				slidesPerColumn: 2,
			},
			// when window width is >= 640px
			1200: {
				slidesPerView: 4,
				slidesPerColumn: 2,
			}
		}
	})
}


