//Product nav active swiper 

function tabActiveCategory1() {
	$('ul#category-mega-nav-1 li a').on("click", function () {
		$(this).parents('ul#category-mega-nav-1').find('li').removeClass('active');
		$(this).parents('ul#category-mega-nav-1 li').addClass('active');
		var display = $(this).attr('data-type');
		console.log(display);
		$("#tab-product-wrapper-1 > .tab-panel-category").removeClass('active');
		$("#tab-product-wrapper-1 > " + "#" + display).addClass('active');

	});

}

