function productInformationShowMore() {
	var button = document.getElementById("show-more-information");
	var content = document.getElementById("article-information");
	//Connecting button variable to on click function
	button.onclick = function () {
		// If content class name is equal to open
		if (content.className == "open") {
			//removes class name
			content.className = "";
			// once removed button shows show more
			button.innerHTML = "Đọc thêm"
		} else {
			//other wise if its closed  button will show show less
			content.className = "open";
			button.innerHTML = "Thu gọn"
		}
	};

}
